<template>
    <div>
        <mu-appbar :title="name" class="headerBar" :style="{background:'rgba(0,0,0,'+opacity+')'}">
            <mu-icon-button icon="chevron_left" slot="left" @click="back()"/>
        </mu-appbar>
        <div class="info scroll-wrapper" :style="{backgroundImage:'url('+coverImgUrl+')'}">
            <loading :show="!success"></loading>
            <div class="list" v-if="success">
                <mu-flexbox :gutter="0">
                    <mu-flexbox-item class="listTitle">
                        <h2>{{name}}</h2>
                        <div class="desc">
                            <span>创建：{{creator.nickname}}</span>
                            <span>播放：{{playCount | formatCount}}次</span>
                        </div>
                    </mu-flexbox-item>
                </mu-flexbox>
                <!--<mu-flexbox v-for="(track,key) in result.tracks" v-if="key<=limit" :gutter="0" class="listItem">-->
                <mu-flexbox v-for="(track,key) in tracks" :gutter="0" class="listItem">
                    <mu-flexbox-item basis="14%" class="listIndex">
                        {{key+1}}
                    </mu-flexbox-item>
                    <mu-flexbox-item class="listInfo" basis="90%">
                        <mu-flexbox :gutter="0">
                            <mu-flexbox-item basis="90%">
                                <div @click="play(track)">
                                    <div class="trackName">{{track.name}}</div>
                                    <div class="textOverflow trackArtist">
                                        <span v-for="(artist,index) in track.artists">
                                            {{index!=0 ? '/':''}}
                                            {{artist.name}}
                                        </span>
                                        <span v-if="track.album">
                                            -{{track.album.name}}
                                        </span>
                                    </div>
                                </div>
                            </mu-flexbox-item>
                            <mu-flexbox-item basis="14%" class="listButton">
                                <mu-icon value="more_vert" color="#979998" :size="28" @click="openBottomSheet(track)"/>
                            </mu-flexbox-item>
                        </mu-flexbox>
                    </mu-flexbox-item>
                </mu-flexbox>
            </div>
            <mu-bottom-sheet :open="bottomSheet" @close="closeBottomSheet">
                <mu-list @itemClick="closeBottomSheet">
                    <mu-sub-header class="textOverflow">
                        歌曲：{{audio.name}}
                    </mu-sub-header>
                    <mu-list-item title="立即播放" @click="play(audio)"/>
                    <mu-list-item title="添加到播放列表" @click="addToList(audio)"/>
                    <mu-list-item title="下载" @click="openDialog"/>
                </mu-list>
            </mu-bottom-sheet>
            <mu-dialog :open="dialog" title="提示">
                当前不提供下载！
                <mu-flat-button label="确定" slot="actions" primary @click="closeDialog"/>
            </mu-dialog>
        </div>
    </div>
</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                success: false,
                dialog:false,
                bottomSheet: false,
                BottomSheetTitle: '请选择',
                opacity: 0.3,
                loadData: false,
                name: '歌单',
                coverImgUrl: '',
                creator: {},
                playCount: 0,
                tracks: [],
                audio:{}
            }
        },
        components: {Loading},
        mounted() {
            let _this = this;
            this.$el.onscroll = function () {
//                console.log('scroll:',document.querySelector('.list').offsetTop)
//                if (document.querySelector('.headerBar')) {
//                    _this.opacity = (document.querySelector('.list').offsetTop) / 150
//                } else {
//                    _this.opacity = 0
//                }
            }
        },
        beforeRouteEnter (to, from, next) {
            next(vm => {
                vm.success = false;
                vm.fetchData();
                if (to.params.name) {
                    vm.name = vm.$route.params.name
                    vm.coverImgUrl = vm.$route.params.coverImgUrl
                }
            })
        },
        methods: {
            back(){
                this.$router.go(-1)
            },
            play(song){
//                if (this.$store.getters.song.id != song.id) {
//                    this.$store.dispatch('getSong', song.id);
//                }
                this.addToList(song);
                this.$store.commit('setSong');
            },
            addToList(song){
                let audio={};
                audio.id = song.id;
                audio.name = song.name;
                audio.artist = song.artists[0].name;
                audio.picUrl= song.album.picUrl;
                audio.songUrl = song.mp3Url;
                this.$store.commit('addSongList', audio);
            },
            downLoad(song){

            },
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/playlist', {params: {id: this.$route.params.id}})
                .then(m => {
                    if (m.data) {
                        let result = m.data.data.result;
                        this.name = result.name;
                        this.coverImgUrl = result.coverImgUrl;
                        this.creator = result.creator;
                        this.playCount = result.playCount;
                        this.tracks = result.tracks;
                        this.success = true;
                    }
                });
            },
            closeBottomSheet () {
                this.bottomSheet = false
            },
            openBottomSheet (val) {
                this.audio = val;
                this.bottomSheet = true
            },
            closeDialog(){
                this.dialog=false;
            },
            openDialog(){
                this.dialog=true;
            }
        }
    }
</script>
<style scoped>
    .padding {
        padding:10px;
    }

    .headerBar {
        position:fixed;
        top:0;
        z-index:10;
        width:100%;
        /*background:rgba(4, 23, 38, 0);*/
        background:#000;
        box-shadow:none
    }

    .mu-appbar .mu-appbar-title {
        font-size:16px;
    }

    .info {
        color:#303030;
        background-repeat:no-repeat;
        background-size:100% auto;
        background-attachment:fixed;
    }

    .info .hd { }

    .info .tit {
        margin-left:10px;
        font-size:12px;
    }

    .list {
        padding-top:70vw;
    }

    .listTitle {
        padding:10px;
        background:-webkit-linear-gradient(top, rgba(4, 23, 38, 0), rgb(240,244,243));
        /*background: rgb(240,244,243);*/
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listTitle h2 {
        margin:0;
        padding:0;
        font-size:20px;
        text-overflow:ellipsis;
        overflow:hidden;
        white-space:nowrap;
    }

    .listTitle span {
        margin-right:10px;
    }

    .listItem {
        font-size:16px;
        background:#F0F4F3;
        color:#303030;
    }

    .listIndex {
        color:#979998;
        font-size:20px;
        text-align:center;
    }

    .listInfo {
        padding:8px 0;
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listItem:nth-of-type(1) {
        border-top:1px solid rgba(255, 255, 255, .2);
    }

    .listItem:last-child .listInfo {
        border:none;
    }

    .listButton {
        align-self:center;
    }
    .trackName{
        color:#303030;
        font-size:24px;
    }
    .trackArtist{
        color:#979998;
        font-size:16px;
    }
</style>



