<template>
    <div>
        <mu-appbar :title="name" class="headerBar" :style="{background:'rgba(0,0,0,'+opacity+')'}">
            <mu-icon-button icon="chevron_left" slot="left" @click="back()"/>
        </mu-appbar>
        <loading :show="!success"></loading>
        <div v-if="success" class="info scroll-wrapper" :style="{backgroundImage:'url('+coverImgUrl+')'}">
            <div class="list">
                <mu-flexbox :gutter="0">
                    <mu-flexbox-item class="listTitle">
                        <h2>暂无API</h2>
                    </mu-flexbox-item>
                </mu-flexbox>
            </div>
        </div>
    </div>
</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                success: false,
                opacity: 0.3,
                name: '歌手',
                coverImgUrl: '',
//                artist: {}
            }
        },
        components: {Loading},
        mounted() {
            let _this = this;
        },
        beforeRouteEnter (to, from, next) {
            next(vm => {
                vm.success = false;
                vm.fetchData();
                if (to.params.name) {
                    vm.name = vm.$route.params.name
                    vm.coverImgUrl = vm.$route.params.picUrl
                }
            })
        },
        methods: {
            back(){
                this.$router.go(-1)
            },
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/artist', {params: {id: this.$route.params.id}})
                .then(m => {
                    if (m.data) {
                        let artist = m.data.data.artist;
                        this.name = artist.name
                        this.coverImgUrl = artist.picUrl
                        this.success = true;
                    }
                });
            },
        }
    }
</script>
<style scoped>
    .padding {
        padding:10px;
    }

    .headerBar {
        position:fixed;
        top:0;
        z-index:10;
        width:100%;
        /*background:rgba(4, 23, 38, 0);*/
        background:#000;
        box-shadow:none
    }

    .mu-appbar .mu-appbar-title {
        font-size:16px;
    }

    .info {
        color:#303030;
        background-repeat:no-repeat;
        background-size:100% auto;
        background-attachment:fixed;
    }

    .info .hd { }

    .info .tit {
        margin-left:10px;
        font-size:12px;
    }

    .list {
        min-height:100%;
        margin-top:80vw;
        background:#f0f4f3;
    }

    .listTitle {
        padding:10px;
        background:-webkit-linear-gradient(top, rgba(4, 23, 38, 0), rgb(240, 244, 243));
        /*background: rgb(240,244,243);*/
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listTitle h2 {
        margin:0;
        padding:0;
        font-size:20px;
        text-overflow:ellipsis;
        overflow:hidden;
        white-space:nowrap;
    }

    .listTitle span {
        margin-right:10px;
    }

    .listItem {
        font-size:16px;
        background:#F0F4F3;
        color:#303030;
    }

    .listIndex {
        color:#979998;
        font-size:20px;
        text-align:center;
    }

    .listInfo {
        padding:8px 0;
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listItem:nth-of-type(1) {
        border-top:1px solid rgba(255, 255, 255, .2);
    }

    .listItem:last-child .listInfo {
        border:none;
    }

    .listButton {
        align-self:center;
    }

    .trackName {
        color:#303030;
        font-size:24px;
    }

    .trackArtist {
        color:#979998;
        font-size:16px;
    }
</style>



