<template>
    <div>
        <mu-appbar :title="name" class="headerBar" :style="{background:'rgba(0,0,0,'+opacity+')'}">
            <mu-icon-button icon="chevron_left" slot="left" @click="back()"/>
        </mu-appbar>
        <div class="info scroll-wrapper" :style="{backgroundImage:'url('+coverImgUrl+')'}">
            <div class="list">
                <loading :show="!success"></loading>
                <mu-flexbox v-for="(song,key) in songs" :gutter="0" class="listItem"  v-if="success">
                    <mu-flexbox-item basis="14%" class="listIndex">
                        {{key+1}}
                    </mu-flexbox-item>
                    <mu-flexbox-item class="listInfo" basis="90%">
                        <mu-flexbox :gutter="0">
                            <mu-flexbox-item basis="90%">
                                <div @click="play(song)">
                                    <div class="trackName">{{song.name}}</div>
                                    <div class="textOverflow trackArtist">
                                        <span v-for="(artist,index) in song.artists">
                                            {{index!=0 ? '/':''}}
                                            {{artist.name}}
                                        </span>
                                    </div>
                                </div>
                            </mu-flexbox-item>
                            <mu-flexbox-item basis="14%" class="listButton">
                                <mu-icon value="more_vert" color="#979998" :size="28"  @click="openBottomSheet(song)"/>
                            </mu-flexbox-item>
                        </mu-flexbox>
                    </mu-flexbox-item>
                </mu-flexbox>
            </div>
            <mu-bottom-sheet :open="bottomSheet" @close="closeBottomSheet">
                <mu-list @itemClick="closeBottomSheet">
                    <mu-sub-header class="textOverflow">
                        歌曲：{{audio.name}}
                    </mu-sub-header>
                    <mu-list-item title="立即播放" @click="play(audio)"/>
                    <mu-list-item title="添加到播放列表" @click="addToList(audio)"/>
                    <mu-list-item title="下载"  @click="openDialog"/>
                </mu-list>
            </mu-bottom-sheet>
            <mu-dialog :open="dialog" title="提示">
                暂时不提供下载！
                <mu-flat-button label="确定" slot="actions" primary @click="closeDialog"/>
            </mu-dialog>
        </div>
    </div>
</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                dialog:false,
                bottomSheet: false,
                BottomSheetTitle: '请选择',
                success: false,
                opacity: 0.3,
                name: '专辑',
                coverImgUrl: '',
                songs:{},
                audio:{}
            }
        },
        components: {Loading},
        mounted() {
            let _this = this;
        },
        beforeRouteEnter (to, from, next) {
            next(vm => {
                vm.success = false;
                vm.fetchData();
                if (to.params.name) {
                    vm.name = vm.$route.params.name;
                    vm.coverImgUrl = vm.$route.params.picUrl;
                }
            })
        },
        methods: {
            back(){
                this.$router.go(-1)
            },
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/album', {params: {id: this.$route.params.id}})
                .then(m => {
                    if (m.data) {
                        let album = m.data.data.album;
                        this.name = album.name;
                        this.coverImgUrl = album.picUrl;
                        this.songs=album.songs;
                        this.success = true;
                    }
                });
            },
            play(song){
                this.addToList(song);
                this.$store.commit('setSong');
            },
            addToList(song){
                let audio={};
                audio.id = song.id;
                audio.name = song.name;
                audio.artist = song.artists[0].name;
                audio.picUrl= song.album.picUrl;
                audio.songUrl = song.mp3Url;
                this.$store.commit('addSongList', audio);
            },
            closeBottomSheet () {
                this.bottomSheet = false
            },
            openBottomSheet (val) {
                this.audio = val;
                this.bottomSheet = true
            },
            closeDialog(){
                this.dialog=false;
            },
            openDialog(){
                this.dialog=true;
            }
        }
    }
</script>
<style scoped>
    .headerBar {
        position:fixed;
        top:0;
        z-index:10;
        width:100%;
        /*background:rgba(4, 23, 38, 0);*/
        background:#000;
        box-shadow:none
    }

    .mu-appbar .mu-appbar-title {
        font-size:16px;
    }

    .info {
        color:#303030;
        background-repeat:no-repeat;
        background-size:100% auto;
        background-attachment:fixed;
    }

    .info .hd { }

    .info .tit {
        margin-left:10px;
        font-size:12px;
    }

    .list {
        min-height:100%;
        margin-top:80vw;
        background:#f0f4f3;
    }

    .listTitle {
        padding:10px;
        background:-webkit-linear-gradient(top, rgba(4, 23, 38, 0), rgb(240, 244, 243));
        /*background: rgb(240,244,243);*/
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listTitle h2 {
        margin:0;
        padding:0;
        font-size:20px;
        text-overflow:ellipsis;
        overflow:hidden;
        white-space:nowrap;
    }

    .listTitle span {
        margin-right:10px;
    }

    .listItem {
        font-size:16px;
        background:#F0F4F3;
        color:#303030;
    }

    .listIndex {
        color:#979998;
        font-size:20px;
        text-align:center;
    }

    .listInfo {
        padding:8px 0;
        border-bottom:1px solid rgba(215, 219, 218, 1);
    }

    .listItem:nth-of-type(1) {
        border-top:1px solid rgba(255, 255, 255, .2);
    }

    .listItem:last-child .listInfo {
        border:none;
    }

    .listButton {
        align-self:center;
    }

    .trackName {
        color:#303030;
        font-size:24px;
    }

    .trackArtist {
        color:#979998;
        font-size:16px;
    }
</style>



